body {
  background-color: black;
  font-family: "furoreregular";
  color: rgb(159, 234, 164);
  text-shadow: rgba(107, 255, 118, 0.8) 0px 0px 30px;
}

.generate {
  background-color: rgb(159, 234, 164);
  padding: 15px;
  border-radius: 25px;
  border: none;
  font-family: "furoreregular";
  /* font-family: "IBM Plex Mono"; */
  font-weight: 700;
  font-size: 15px;
  margin-top: 30px;
  cursor: pointer;
}

.fa {
  padding: 5px;
  font-size: 20px;
  width: 80px;
  text-decoration: none;
  margin: 5px 2px;
  border-radius: 15px;
}

.fa-twitter {
  background: #55acee;
  color: white;
  font-size: 18px;
  /* font-family:; */
}

.fa:hover {
  opacity: 0.7;
}

.fa-twitter:before {
  /* content: "\f099"; */
  margin-right: 5px;
}

.poster {
  height: 500px;
}

.heading {
  font-family: "furoreregular";
}

.text {
  border: 1px solid;
  letter-spacing: 2px;
  padding: 10px;
  width: 700px;
}
